import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { IonSlides, ToastController } from '@ionic/angular';
import { AuthConstants } from 'src/app/configuration/auth-constants';
import { IPO } from 'src/app/models/IPO';
import { IUser } from 'src/app/models/IUser';
import { HttpService } from 'src/app/services/http.service';
import { StorageService } from 'src/app/services/storage.service';

@Component({
  selector: 'app-product-orders',
  templateUrl: './product-orders.page.html',
  styleUrls: ['./product-orders.page.scss'],
})
export class ProductOrdersPage implements OnInit {
  @ViewChild('slider', { static: false }) slider: IonSlides;

  ops = {
    slidesPerView: 1,
    spaceBetween: 0,
    centeredSlides: true,
    centeredSlidesBounds: true,
    speed: 500,
    freeMode: false,
    freeModeSticky: true,
    preventClicks: true,
  };
  
  data_loaded: boolean = false;
  segment: string = 'process';
  po: IPO;
  pos0: IPO[];
  pos2: IPO[];
  new: boolean = false;
  user: IUser;

  constructor(
    private router: Router,
    public toastController: ToastController,
    private httpClient: HttpService,
    private storageService: StorageService
  ) { }

  ngOnInit() {
    
  }

  ionViewWillEnter(){
    this.httpClient.getPOs()
      .subscribe(po => {
        console.log(po);
        this.po = po.filter(po => po.state === 1)[0];
        this.pos0 = po.filter(po => po.state === 0);
        this.pos2 = po.filter(po => po.state === 2);
        console.log(this.po, this.pos0, this.pos2)
        this.data_loaded = true;
      }, error => console.log(error))
  }

  segmentChanged(event){
    let key = event.detail.value;
    this.segment = key;
    switch (key) {
      case 'process':
        this.slider.slideTo(0)
        break;
      case 'pending':
        this.slider.slideTo(1)
        break;
      case 'closed':
        this.slider.slideTo(2)
        break;
      default:
        break;
    }
  }

  newPO(){
    this.router.navigate(['product-order-detail/']);
  }

  slideChanged() {
    this.slider.getActiveIndex().then(data => {
      switch (data) {
        case 0:
          this.segment = 'process';
          break;
        case 1:
          this.segment = 'pending';
          break;
        case 2:
          this.segment = 'closed';
          break;
        default:
          break;
      }
      
    })
  }

  slideTouched() {
    this.slider.getSwiper().then((swiper) => {
      this.slider.slideTo(swiper.clickedIndex);
    });
  }

  goBack(): void {
    this.router.navigate(['home']);
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Atrás' : 'Atrás';
  }


}
